@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial: 16px;
  --prime-color:#e81230;
  --donation-prime-color:#BF5083;
  --startPage-background-color:#FFF6F3;
  --donation-background-color:rgb(255, 191, 191); }

* {
  margin: 0;
  padding: 0; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.35 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 16px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 18px); } }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em; }

h5, h6 {
  font-weight: bold; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 2em; }

li {
  list-style-position: inside; }

a {
  text-decoration: none; }

input, textarea, select, button, option {
  font: var(--font-size-initial, 16px)/1.35 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

.btn {
  display: inline-block;
  border: 3px solid var(--prime-color, #e81230);
  border-radius: 5px;
  background: none;
  padding: 0.7em 2em;
  color: var(--prime-color, #e81230);
  text-decoration: none;
  font-size: 1.2em;
  text-align: center; }

.pennant {
  position: relative;
  display: inline-block;
  padding: 0.175em;
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  cursor: default;
  text-align: center;
  vertical-align: baseline; }

.icon {
  position: relative;
  display: inline-block;
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  color: var(--prime-color, #e81230);
  text-decoration: none;
  width: 2em;
  height: 2em;
  text-align: center;
  line-height: 2em; }
  @media screen and (min-width: 500px) {
    .icon {
      font-size: 1.2em; } }

.icon:before {
  display: inline-block; }

.startPage-btn {
  padding: 0.5em 1em;
  color: var(--prime-color, #e81230);
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 25px; }

.icon-nav {
  border: 1px solid var(--prime-color, #e81230);
  border-radius: 50%;
  color: var(--prime-color, #e81230);
  padding: 0.5em;
  text-align: center; }
  @media screen and (min-width: 500px) {
    .icon-nav {
      font-size: 1.2em; } }

