.tabs {
  height: 43em;
  width: 100%;
  clear: both;
  position: relative;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  background: white;
  overflow: hidden; }

.tabs-main {
  height: 100em;
  width: 100%;
  clear: both;
  position: relative;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  background: white;
  overflow: hidden; }

.tab p {
  padding: 1em;
  width: 100%;
  margin: 1em 0;
  text-align: left; }

.tab label {
  position: relative;
  background: white;
  color: var(--prime-color, #e81230);
  padding: 0.1em;
  font-size: 2em;
  display: grid; }

.tab-bodk-main label {
  font-size: 1.2em; }

.tab [type=radio] {
  display: none; }

.content {
  position: absolute;
  top: 8em;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  width: 100%;
  text-align: center;
  margin: 0 auto; }

.content-bodk-main {
  position: absolute;
  top: 5em;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  text-align: left;
  width: 100%; }

[type=radio]:checked ~ label {
  z-index: 2; }

[type=radio]:checked ~ label ~ .content {
  z-index: 1; }

[type=radio]:checked ~ label ~ .content-bodk-main {
  z-index: 1; }

.tab-title {
  font-size: 0.8em;
  opacity: 0; }

.tab label:hover span {
  opacity: 1; }

.wrapper {
  display: grid;
  grid-template-areas: "header" "main" "footer"; }
  .wrapper header {
    grid-area: header;
    justify-self: center;
    align-self: start; }
  .wrapper main {
    grid-area: main;
    justify-self: center;
    align-self: start;
    width: 100%;
    padding: 2em; }
  .wrapper footer {
    grid-area: footer;
    justify-self: center;
    align-self: start; }

.bodk-main-banner {
  width: 100%; }

#aboutUs {
  background: white; }

#home_text {
  background: var(--prime-color, #e81230);
  width: 100%;
  padding: 2em;
  margin: 5em auto; }

#recipient img {
  width: 50%; }
  @media screen and (min-width: 701px) {
    #recipient img {
      width: 25%; } }

.cover {
  background-image: url("/images/FON.svg");
  background-position: center center;
  background-origin: padding-box;
  background-repeat: no-repeat;
  background-size: auto;
  z-index: -100;
  border-bottom: 3px solid var(--prime-color, #e81230);
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  padding: 1em 4em;
  width: 100%; }

header {
  width: 100%; }

header ul {
  list-style: none inside;
  padding: 0; }

header ul li {
  display: inline-block;
  margin: 0.1em; }

.home-text {
  margin: 0 0.1em;
  text-align: center; }

.home-text img {
  background: var(--prime-color, #e81230);
  padding: 1em;
  width: 100%; }

.social-net {
  display: flex;
  align-content: center;
  align-items: center;
  flex-direction: column; }

.phone {
  color: var(--prime-color, #e81230);
  font-size: 1.2em;
  padding: 0.2em; }

.cover-footer {
  background-image: url("/images/FON.svg");
  background-position: unset;
  background-repeat: no-repeat;
  z-index: -100;
  border-top: 3px solid var(--prime-color, #e81230);
  padding: 1em;
  text-align: center;
  width: 100%; }

footer {
  width: 100%; }

.contactForm {
  text-align: center; }

#contact-form {
  width: 100%;
  padding: 2em;
  text-align: center;
  margin: 0 auto; }
  @media screen and (min-width: 900px) {
    #contact-form {
      width: 70%; } }

#contact-form fieldset {
  border: none; }

#contact-form label {
  float: left;
  color: #e81230;
  display: none; }

#contact-form input[type=text],
#contact-form input[type=email],
#contact-form select,
#contact-form textarea {
  width: 100%;
  background: white;
  height: 3em;
  resize: none;
  border: none;
  border-bottom: 1.35px solid var(--prime-color, #e81230);
  margin: 1em 0; }

#contact-form textarea {
  resize: vertical;
  height: 100px; }

#contact-form input[type=submit] {
  background-color: #4CAF50;
  color: white;
  padding: 1em 2em;
  margin: 1em 0;
  border: none;
  border-radius: 5px;
  cursor: pointer; }

#contact-form input[type=text]:hover,
#contact-form input[type=email]:hover,
#contact-form select:hover,
#contact-form textarea:hover {
  transition: border-color 0.3s ease-in-out;
  border-bottom: 1px solid #AAA; }

#contact-form input[type=submit]:hover {
  transition: border-color 0.3s ease-in-out;
  border: 1px solid #AAA;
  background-color: #45a049; }

#contact-form input[type=submit]:active {
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.5); }

#contact-form input:focus,
#contact-form textarea:focus {
  outline: 0;
  border: 1px solid #999; }

.donation-header-top {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 2em; }
  .donation-header-top .icon-nav {
    color: var(--donation-background-color, #ffbfbf);
    border: 1px solid var(--donation-background-color, #ffbfbf); }
  .donation-header-top .icon {
    color: var(--donation-background-color, #ffbfbf);
    border: 1px solid var(--donation-background-color, #ffbfbf); }
  .donation-header-top .pennant {
    border: 1px solid var(--donation-background-color, #ffbfbf); }

.donation-title {
  font-style: italic;
  text-align: center; }

.donation-nav {
  display: block;
  background: var(--donation-background-color, #ffbfbf);
  text-align: center; }
  .donation-nav ul li {
    list-style: none inside;
    padding: 0.1em;
    font-size: 1.2em; }
    @media screen and (max-width: 700px) {
      .donation-nav ul li {
        display: block;
        text-align: left; } }
  .donation-nav a {
    text-transform: uppercase;
    font-style: italic;
    color: black;
    text-decoration: none; }

.donation-footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  background: var(--donation-background-color, #ffbfbf); }
  .donation-footer img {
    width: 15%; }
  .donation-footer p {
    font-style: italic;
    font-size: 1.2em; }
